import React from "react"
import { Link } from "gatsby"
import { css } from "@emotion/core"

import PrettyImage from "../components/pretty-image"

function BlogPostPreview(props) {
  return (
    <article
      css={css`
        padding-bottom: 40px;
        width: 100%;
        position: relative;
        &:hover {
          & h3 {
            color: var(--color-interactive-light);
            transition: all 100ms linear;
            -webkit-transition: all 100ms linear;
          }
        }
      `}
    >
      <Link
        css={css`
          position: relative;
          display: flex;
          text-decoration: none;
          color: var(--color-text);
        `}
        to={props.node.fields.slug}
      >
        <div
          css={css`
            display: grid;
            grid-template: "title title title title title date" "description description description description description description" "excerpt excerpt excerpt excerpt excerpt excerpt";
            width: 80%;
            padding-right: 25px;
            @media (max-width: 576px) {
              width: 100%;
              padding-right: 0px;
            }
          `}
        >
          <h3
            css={css`
              margin-bottom: 7px;
              grid-area: title;
            `}
          >
            {props.node.frontmatter.title}
          </h3>
          <h6
            css={css`
              margin-bottom: 7px;
              font-weight: bold;
              color: var(--color-off-text);
              text-align: right;
              letter-spacing: 1px;
              grid-area: date;
            `}
          >
            {props.node.frontmatter.date}
          </h6>
          <h4
            css={css`
              margin-bottom: 10px;
              color: var(--color-darker-primary);
              grid-area: description;
            `}
          >
            {props.node.frontmatter.description}
          </h4>
          <p
            css={css`
              grid-area: excerpt;
            `}
          >
            {props.node.excerpt}
          </p>
        </div>
        <div
          css={css`
            display: var(--no-phone-block);
            width: 20%;
          `}
        >
          <PrettyImage
            fluid={props.node.frontmatter.thumbnail.childImageSharp.fluid}
          />
        </div>
      </Link>
    </article>
  )
}

export default BlogPostPreview

