const leftRightGrid = css`
  display: grid;
  grid-template-areas: var(--left-right-grid-areas);
  grid-column-gap: 70px;
`

const gradientText = css`
  background: var(--color-accent-gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  letter-spacing: 1.3px;
`

const icon = css`
  margin-left: 10px;
  margin-right: 10px;
  stroke: var(--color-interactive-dark);
  &:hover {
    fill: var(--color-interactive-light-50);
    cursor: pointer;
  },
`

function RecommendedListItem({ link, children }) {
  return (
    <PrettyListitem>
      <h4>
        <a
          css={css`
            text-decoration: none;
            color: var(--color-text);
            &:hover {
              color: var(--color-interactive-light);
              transition: all 100ms linear;
              -webkit-transition: all 100ms linear;
            },
          `}
          href={link}
        >
          {children}
        </a>
      </h4>
    </PrettyListitem>
  )
}

